<template>
  <div class="MealList">
    <div class="weui-flex">
      <div class="weui-flex__item" v-for="(item ,index) in Mealtype" :key="index">
        <div class="placeholder" :class="{active:item==isactive}" @click="tabClick(item)">{{item}}</div>
      </div>
    </div>
    <div class="tabcontent" v-show="isactive=='早餐'">
      <div class="weui-cells">
        <div class="weui-cell" v-for="(item,index) in breakfastData" :key="index">
          <div class="weui-cell__hd" style="position: relative;margin-right: 10px;">
            <img :src="item.foodImage">
          </div>
          <div class="weui-cell__bd">
            <h3>{{item.name}}</h3>
            <p>
              <template v-if="item.foodProvides==0">本酒店</template>
              <template v-if="item.foodProvides==1">{{item.foodProvidesName}}</template>
            </p>
            <p class="time">配送时间{{item.deliveryTime}}分钟</p>
          </div>
          <div class="weui-cell__ft">
            <div class="Price">
              ￥
              <span>{{item.price|money}}</span>
            </div>
            <InputNumber :num="item.num" :targetdata="item" :changefn="changefn"></InputNumber>
          </div>
        </div>
      </div>
      <div class="notAvailable" v-if="breakfastData.length==0">
        <img src="../assets/images/expression.png" alt="">
        <span>当前暂未提供该时段菜品</span>
      </div>
    </div>
    <div class="tabcontent" v-show="isactive=='中餐'">
      <div class="weui-cells">
        <div class="weui-cell" v-for="(item,index) in lunchData" :key="index">
          <div class="weui-cell__hd" style="position: relative;margin-right: 10px;">
            <img :src="item.foodImage">
          </div>
          <div class="weui-cell__bd">
            <h3>{{item.name}}</h3>
            <p>
              <template v-if="item.foodProvides==0">本酒店</template>
              <template v-if="item.foodProvides==1">{{item.foodProvidesName}}</template>
            </p>
            <p class="time">配送时间{{item.deliveryTime}}分钟</p>
          </div>
          <div class="weui-cell__ft">
            <div class="Price">
              ￥
              <span>{{item.price|money}}</span>
            </div>
            <InputNumber :targetdata="item" :num="item.num" :changefn="changefn"></InputNumber>
          </div>
        </div>
      </div>
      <div class="notAvailable" v-if="lunchData.length==0">
        <img src="../assets/images/expression.png" alt="">
        <span>当前暂未提供该时段菜品</span>
      </div>
    </div>
    <div class="tabcontent" v-show="isactive=='晚餐'">
      <div class="weui-cells">
        <div class="weui-cell" v-for="(item,index) in dinnerData" :key="index">
          <div class="weui-cell__hd" style="position: relative;margin-right: 10px;">
            <img :src="item.foodImage">
          </div>
          <div class="weui-cell__bd">
            <h3>{{item.name}}</h3>
            <p>
              <template v-if="item.foodProvides==0">本酒店</template>
              <template v-if="item.foodProvides==1">{{item.foodProvidesName}}</template>
            </p>
            <p class="time">配送时间{{item.deliveryTime}}分钟</p>
          </div>
          <div class="weui-cell__ft">
            <div class="Price">
              ￥
              <span>{{item.price|money}}</span>
            </div>
            <InputNumber :num="item.num" :targetdata="item" :changefn="changefn"></InputNumber>
          </div>
        </div>
      </div>
      <div class="notAvailable" v-if="dinnerData.length==0">
        <img src="../assets/images/expression.png" alt="">
        <span>当前暂未提供该时段菜品</span>
      </div>
    </div>
    <div class="tabcontent" v-show="isactive=='宵夜'">
      <div class="weui-cells">
        <div class="weui-cell" v-for="(item,index) in supperData" :key="index">
          <div class="weui-cell__hd" style="position: relative;margin-right: 10px;">
            <img :src="item.foodImage">
          </div>
          <div class="weui-cell__bd">
            <h3>{{item.name}}</h3>
            <p>
              <template>{{item.ProvidesName}}</template>
            </p>
            <p class="time">配送时间{{item.deliveryTime}}分钟</p>
          </div>
          <div class="weui-cell__ft">
            <div class="Price">
              ￥
              <span>{{item.price|money}}</span>
            </div>
            <InputNumber :num="item.num" :targetdata="item" :changefn="changefn"></InputNumber>
          </div>
        </div>
      </div>
      <div class="notAvailable" v-if="supperData.length==0">
        <img src="../assets/images/expression.png" alt="">
        <span>当前暂未提供该时段菜品</span>
      </div>
    </div>
    <div class="mask" v-if="showShopping" @click="showShopping=false"></div>
    <footer class="Shopping">
      <transition name="dynamic">
        <div class="Shopping-details" v-if="showShopping">
          <div class="top">
            <span @click="emptyfn">
              <svg class="icon" aria-hidden="true">
                <use xlink:href="#icon-weibiaoti--"></use>
              </svg>
              清空购物车</span>
          </div>
          <ul class="list">
            <li class="clearfix" v-for="(item,index) in SelectedData" :key="index">
              <span class="name fl">{{item.foodName}}</span>
              <InputNumber :num="item.foodNumber" :targetdata="item" :changefn="carchangefn"></InputNumber>
              <span class="money fr">￥{{item.foodPrice|money}}</span>
            </li>
          </ul>
        </div>
      </transition>
      <div class="car fl" @click="showShoppingfn">
        <svg class="icon" aria-hidden="true">
          <use xlink:href="#icon-cart"></use>
        </svg>
        <span>{{carDdata.num}}</span>
      </div>
      <div class="Price fl">￥{{carDdata.Price|money}}</div>
      <a href="jascript:;" @click="Settlementfn" class="weui-btn weui-btn_primary fr">
        去结算
      </a>
    </footer>
  </div>
</template>

<script type='text/ecmascript-6'>
import InputNumber from "@/components/InputNumber";
import { foodBooking } from "@/assets/js/request.js";

export default {
  data() {
    return {
      Mealtype: ["早餐", "中餐", "晚餐", "宵夜"],
      isactive: "早餐",
      showShopping: false,
      hotelId: this.$store.state.hotelId,
      hotelName: this.$store.state.HotelInfo.name,
      breakfastData: [],
      lunchData: [],
      dinnerData: [],
      supperData: [],
      SelectedData: [],
      carDdata: {
        num: 0,
        Price: 0
      }
    };
  },
  methods: {
    //切换tab栏
    tabClick(item) {
      this.isactive = item;
    },
    //去重
    abc(arr, item, flag) {
      for (var i = 0; i < arr.length; i++) {
        if (flag) {
          if (arr[i].foodId == item.foodId) {
            return i;
          }
        } else {
          if (arr[i].id == item.foodId) {
            return i;
          }
        }
      }
      return -1;
    },
    //数字改变的方法
    changefn(val, item, flag) {
      var newfood = {
        foodId: item.id,
        foodName: item.name,
        foodNumber: val,
        foodPrice: item.price,
        foodProvidesName: item.foodProvidesName,
        foodProvidesPhone: item.orderReceivePhone
      };
      var index = this.abc(this.SelectedData, newfood, true);
      if (index != -1) {
        this.SelectedData[index].foodNumber = val;
      } else {
        this.SelectedData.push(newfood);
      }
      var rule1 = this.abc(this.breakfastData, newfood);
      if (rule1 != -1) {
        this.breakfastData[rule1].num = val;
      }
      var rule2 = this.abc(this.lunchData, newfood);
      if (rule2 != -1) {
        this.lunchData[rule2].num = val;
      }
      var rule3 = this.abc(this.dinnerData, newfood);
      if (rule3 != -1) {
        this.dinnerData[rule3].num = val;
      }
      var rule4 = this.abc(this.supperData, newfood);
      if (rule4 != -1) {
        this.supperData[rule4].num = val;
      }
      this.countfn();
    },
    //计算方法
    countfn() {
      this.carDdata.num = 0;
      this.carDdata.Price = 0;
      this.SelectedData.forEach(function(item) {
        this.carDdata.num += item.foodNumber;
        this.carDdata.Price += item.foodNumber * item.foodPrice;
      }, this);
    },
    //打开购物车
    showShoppingfn() {
      if (this.carDdata.num != 0) {
        this.showShopping = !this.showShopping;
      }
    },
    //改变购物车数字
    carchangefn(val, item, flag) {
      item.foodNumber = val;
      if (val == 0) {
        this.SelectedData.splice(this.SelectedData.indexOf(item), 1);
      }
      var rule1 = this.abc(this.breakfastData, item);
      if (rule1 != -1) {
        this.breakfastData[rule1].num = val;
      }
      var rule2 = this.abc(this.lunchData, item);
      if (rule2 != -1) {
        this.lunchData[rule2].num = val;
      }
      var rule3 = this.abc(this.dinnerData, item);
      if (rule3 != -1) {
        this.dinnerData[rule3].num = val;
      }
      var rule4 = this.abc(this.supperData, item);
      if (rule4 != -1) {
        this.supperData[rule4].num = val;
      }
      this.countfn();
      if (this.carDdata.num == 0) {
        this.showShopping = false;
      }
    },
    //清空购物车
    emptyfn() {
      this.carDdata.num = 0;
      this.carDdata.Price = 0;
      this.SelectedData = [];
      this.breakfastData.forEach(function(item) {
        item.num = 0;
      }, this);
      this.lunchData.forEach(function(item) {
        item.num = 0;
      }, this);
      this.dinnerData.forEach(function(item) {
        item.num = 0;
      }, this);
      this.supperData.forEach(function(item) {
        item.num = 0;
      }, this);
      this.showShopping = false;
    },
    Settlementfn() {
      if (this.SelectedData.length == 0) {
        this.Toast("请选择");
      } else {
        foodBooking.foodsettlement({
          Suffix: this.hotelId + "/settlement",
          params: {
            billPrice: this.carDdata.Price,
            foodTotalPrice: this.carDdata.Price,
            foods: this.SelectedData,
            hotelId: this.hotelId,
            hotelName: this.hotelName,
            realPrice: this.carDdata.Price,
            receivablePrice: this.carDdata.Price
          },
          fn: data => {
            console.log(data);
            this.$router.push("/defrayal/" + data.orderId);
          }
        });
      }
    }
  },
  components: {
    InputNumber
  },
  mounted() {
    foodBooking.foodlist({
      Suffix: this.hotelId,
      fn: data => {
        var time_to_sec = function(time) {
          var s = "";
          var hour = time.split(":")[0];
          var min = time.split(":")[1];
          var sec = time.split(":")[2];

          s = Number(hour * 3600) + Number(min * 60) + Number(sec);
          return s;
        };
        var now = new Date();
        var hh = now.getHours(); //时
        var mm = now.getMinutes(); //分
        var ss = now.getSeconds(); //秒
        var nowTime = time_to_sec(hh + ":" + mm + ":" + ss);

        data.records.forEach(function(item) {
          if (item.foodProvides == 0) {
            item.ProvidesName = "本酒店";
          } else {
            item.ProvidesName = item.foodProvidesName;
          }
          item.num = 0;
          if (item.breakfastEnable == 0) {
            var breakfastBegin = time_to_sec(item.breakfastBegin + ":00");
            var breakfastEnd = time_to_sec(item.breakfastEnd + ":00");
            if (breakfastBegin <= nowTime && nowTime <= breakfastEnd) {
              this.breakfastData.push(item);
            }
          }
          if (item.lunchEnable == 0) {
            var lunchBegin = time_to_sec(item.lunchBegin + ":00");
            var lunchEnd = time_to_sec(item.lunchEnd + ":00");
            if (lunchBegin <= nowTime && nowTime <= lunchEnd) {
              this.lunchData.push(item);
            }
          }
          if (item.dinnerEnable == 0) {
            var dinnerBegin = time_to_sec(item.dinnerBegin + ":00");
            var dinnerEnd = time_to_sec(item.dinnerEnd + ":00");
            if (dinnerBegin <= nowTime && nowTime <= dinnerEnd) {
              this.dinnerData.push(item);
            }
          }
          if (item.supperEnable == 0) {
            var supperBegin = time_to_sec(item.supperBegin + ":00");
            var supperEnd = time_to_sec(item.supperEnd + ":00");
            if (supperBegin <= nowTime && nowTime <= supperEnd) {
              this.supperData.push(item);
            }
          }
        }, this);
      }
    });
  }
};
</script>

<style scoped lang='less' rel='stylesheet/less'>
/* 可以设置不同的进入和离开动画 */

/* 设置持续时间和动画函数 */

.dynamic-enter-active {
  transition: all 0.5s ease;
}

.dynamic-leave-active {
  transition: all 0.5s ease;
}

.dynamic-enter,
.dynamic-leave-to {
  opacity: 0;
  transform: translateY(100%);
}
</style>



