<template>
    <div class="Calculation fr">
        <span class="minus" @click="minusNum">-</span>
        <span class="num">{{numdata}}</span>
        <span class="add" @click="addNum">+</span>
    </div>
</template>

<script type='text/ecmascript-6'>
export default {
  data() {
    return {
      numdata: this.num
    };
  },
  methods: {
    minusNum() {
      if (this.minnum && this.numdata <= this.minnum) {
        return false;
      } else {
        if (this.numdata <= 0) {
          return false;
        }
      }
      this.numdata--;
      if (this.changefn) {
        this.changefn(this.numdata, this.targetdata, true);
      }
    },
    addNum() {
      if (this.maxnum && this.numdata >= this.maxnum) {
        return false;
      }
      this.numdata++;
      if (this.changefn) {
        this.changefn(this.numdata, this.targetdata, false);
      }
    }
  },
  props: ["num", "minnum", "maxnum", "changefn", "targetdata"],
  watch: {
    num: function() {
      this.numdata = this.num;
    }
  },
  mounted() {
    if (!this.num) {
      this.numdata = 0;
    }
  }
};
</script>

<style scoped lang='less' rel='stylesheet/less'>
.Calculation {
  font-size: 0;
  padding-right: 15px;
  .minus,
  .add {
    display: inline-block;
    width: 0.56rem;
    height: 0.56rem;
    line-height: 0.56rem;
    text-align: center;
    border-radius: 50%;
    background: #4499ff;
    font-size: 0.33rem;
    font-weight: bold;
    color: #fff;
    vertical-align: top;
  }
  .num {
    font-size: 0.33rem;
    display: inline-block;
    min-width: 0.8rem;
    box-sizing: border-box;
    padding: 0 0.1rem;
    height: 0.56rem;
    line-height: 0.56rem;
    border-radius: 3px;
    text-align: center;
    background: #f4f4f4;
    margin: 0 0.2rem;
    color: #2b2b2b;
    vertical-align: top;
  }
}
</style>



